.popup {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    visibility: hidden;
    transition: visibility 0s ease, opacity .4s ease;
    opacity: 0;
    justify-content: center;
    align-items: center;
}